import React, { useState } from "react";
import styled from "styled-components";
import BookmarkOff from "../../assets/icons/UnBookmarkIcon.svg";
import BookmarkOn from "../../assets/icons/BookmarkIcon.svg";
import { useAuthStore } from "../../store/useAuthStore";

interface BookMarkIconButtonProps {
  handleBookmark: () => void; //    
  isBookmarked: boolean;
}

const BookMarkIconButton: React.FC<BookMarkIconButtonProps> = ({
  handleBookmark,
  isBookmarked,
}) => {
  const isAuthenticated = useAuthStore((s) => s.isAuthenticated);

  const handleClick = () => {
    if (!isAuthenticated) {
      alert("   .");
      return;
    }

    handleBookmark(); //    (API  )
  };

  return (
    <Button onClick={handleClick} aria-label="  ">
      {isBookmarked ? (
        <img src={BookmarkOn} alt="" />
      ) : (
        <img src={BookmarkOff} alt="  " />
      )}
    </Button>
  );
};

export default BookMarkIconButton;

// ================== styled-components ==================

const Button = styled.button`
  background: none;
  border: none;
  padding: 4px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 10px;

  img {
    width: 24px;
    height: 24px;
  }

  &:active {
    transform: scale(0.95);
  }
`;
